.lection_container {
    max-width: 70%;
    margin-left: 350px;
    margin-right: 100px;
}

.lection_btn {
    width: 240px;
    height: 40px;
    border-radius: 10px;
    font-size: 18px;
    cursor: pointer;    
    border-radius: 8px;    
    text-transform: uppercase;   
    font-weight: 600; 
    margin-top: 100px;
    color: #484848;
    background-color: rgb(51, 234, 51); 
    border: 1px solid rgb(51, 234, 51);
    box-shadow: 1px 3px 3px rgb(44, 205, 44);   
    transition: all .1s ease;    
}

.lection_btn:hover {
    background-color: rgb(47, 220, 47);
}