.code-block {
    background-color: #C8C8C8;
}

.top-pane {
    background-color: #b3fdb3;
}

.pane {
    height: 50vh;
    display: flex;    
}

.editor-container {
    flex-grow: 1;
    flex-basis: 0;    
    display: flex;
    flex-direction: column;
    padding: .5rem;
    background-color: #b3fdb3;
}

.editor-container.collapsed {
    flex-grow: 0;
}

.editor-container.collapsed .CodeMirror-scroll {
    position: absolute;
    overflow: hidden !important;
}

.expand-collapse-btn {
    margin-left: 10px;
    background: none;
    border: none;
    color: rgb(227, 238, 229);
    cursor: pointer;
}

.editor-title {
    display: flex;
    justify-content: space-between;
    background-color: #91de91;
    padding: 10px 10px 10px 10px;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
}

.CodeMirror {
    height: 100% !important;
}

.code-mirror-wrapper {
    flex-grow: 1;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
    overflow: hidden;
}


/* --------------------------- */

.lesson_task-sidebar{
    display: flex;
    flex-direction: column;
    align-items: left;
    width: 270px;
    float: left;
    height: 100vh;
    top: 0;
    left: 0;
    margin-left: 5px;
    border-right: 2px solid rgb(232, 232, 232);
}

.lesson_task-title {
    color: #3c3c3c;
}

.lesson_task-btn {
    height: 40px;
    width: 230px;
    margin-left: 20px;
    margin-bottom: 10px;
    font-size: 16px;
    font-weight: 600;
    text-transform: uppercase;
    background-color: #ebebeb;
    color: #929292;
    border: 2px solid #cbcbcb;
    border-radius: 10px;
    cursor: pointer;
    transition: all .1s ease;
}

.lesson_task-btn:hover {
    background-color: #e4e3e3;
    color: #838383;
}