.answer_form {
    display:flex;
    justify-content:center;
    align-items:center;
    flex-direction: column;
    height: 75vh;
}

.question-condition {
    color: white;
    text-align: center;
    font-size: 38px;
    background-color: #1e2749;
}

.question-btn_yes, .question-btn_no {
    width: 48%;
    height: 40px;        
    border-radius: 10px;
    font-size: 18px;    
    cursor: pointer;
    background-color: #c6ccd8;  
    border: 3px solid #b6bdcb;
    box-shadow: 1px 3px 3px #b6bdcb;
    border-radius: 8px;
    transition: all .1s ease;
}

.question-btn_yes {
    margin-right: 4%;
}

.question-btn_yes:hover, .question-btn_no:hover {
    background-color: #d5d9e4;
}