.background {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background-image: url("../../assets/images/background1.png");
    z-index: -1;
}

.register_form-container {
    display:flex;
    justify-content:center;
    align-items:center;
    flex-direction: column;
    height: 75vh;
}

.register-title {
    font-weight: bold;
    font-size: 38px;
    background-color: #1e2749;
    color: white;
}

.email-reg, .password-reg, .submit-reg {
    width: 450px;
    height: 40px;
    margin-bottom: 10px;
    border-radius: 10px;
}

.email-reg {
    background-color: #c6ccd8;
    padding-left: 10px;
    transition: all .1s ease;
}

.password-reg {
    background-color: #c6ccd8;
    padding-left: 10px;
    transition: all .1s ease;
}

.submit-reg {
    font-size: 18px;    
    color: white;
    cursor: pointer;
    background-color: #8fa7c8 ; 
    border: 3px solid #8198b8 ;
    box-shadow: 1px 3px 3px #8198b8;
    border-radius: 8px;
    transition: all .1s ease;
}

.submit-reg:hover {
    background-color: #99b6df;
}

.email-reg:focus, .password-reg:focus {
    outline: none;
    background-color: #f0e9e9;
}