body {
    background-image: none !important;
}

.book-title_wrap {
    margin-bottom: 10px;
    margin-left: 20px;
    align-self: flex-start;
}

.book-title {
    color: #1dca11;
    font-size: 30px;    
}

.book_sidebar {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: fixed;
    width: 250px;
    float: left;
    height: 100vh;
    top: 0;
    left: 0;
    border-right: 2px solid rgb(232, 232, 232);
}

.book_sidebar-btn {
    display: flex;
    align-items: center;
    font-size: 16px;
    background-color: white;
    color: #999999;    
    width: 220px;
    height: 50px;
    cursor: pointer;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 1px;
    margin-bottom: 10px;
    border-radius: 12px;
    transition: all .1s ease;
}

.book_sidebar-btn_icon {
    margin-left: 20px;
    font-size: 20px;
}

.book_sidebar-btn span {
    margin-left: 20px;
}

.book_sidebar-btn:hover {
    background-color: #dff3ff;
}

.book_sidebar-btn-selected {
    border: 2px solid #89d2ff;
    color: #3d9ae6;
    /* box-shadow: 1px 3px 3px #2196F3; */
    background-color: #dff3ff;
}

.sidebar_sort-navlink {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: auto;
    margin-bottom: 50px;
}


.book_content-wrap {
    display: flex;
    justify-content: center;
    margin-left: 300px;
    margin-right: 50px;
}


.book_content {
    display: flex;
    flex-direction: column;
}

.section_header {
    display: flex;
    justify-content: space-between;
    min-width: 100%;
    background-color: #1fce13;
    color: white;
    border-radius: 20px;
    padding-top: 22px;
    padding-bottom: 22px;
    padding-left: 12px;
    margin-bottom: 60px;
}

.section_header-info {
    max-width: 50%;
}

.section_header-title {
    padding: 0;
    margin: 0;
    margin-bottom: 10px;
    font-weight: 500;
    font-size: 28px;
}

.section_header-topic {
    font-size: 19px;
}

.section_header-guide {
    border: 1px solid #0a5904;
    border-radius: 15px;
    box-shadow: 0px 2px 2px #0a5904;
    height: 25px;
    align-self: center;
    cursor: pointer;    
    font-size: 18px;
    text-transform: uppercase;
    padding: 13px 10px 10px 10px;
    margin-right: 12px;
    transition: all .1s ease;
}

.section_header-guide:hover {
    background-color: #1fca13;
}

.section_header-guide_icon {
    margin-right: 10px;
}

.bookMark_icon {
    font-size: 20px;    
}

.section_content {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.lesson_icon {
    font-size: 30px;
}
