.background {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background-image: url("../../assets/images/background1.png");
    z-index: -1;
}

.header {
    /* display: flex;
    justify-content: space-between; */
    padding-top: 20px;
    background-color: #1e2749;
}

.title-name {
    color: white;
    font-size: 38px;
}

.main-container {
    position: absolute;
    display: flex;
    /* justify-content: center;
    align-items: center; */
    height: 75%;
}

.main {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 200px;
}

.planet-img {
    background: url("../../assets/images/planet.jpg");
    background-size: 330px;
    height: 330px;
    min-width: 330px;
    width: 330px;
}

.form-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.title-paragraph {
    color: white;
    font-size: 38px;
    max-width: 700px;
    font-weight: bold;
    text-align: center;
}

.registration_btn, .login_btn {
    width: 400px;
    height: 50px;
    color: white;
    font-size: 20px;
    cursor: pointer;
    font-weight: bold;
    text-transform: uppercase;
    border-radius: 10px;
}

.registration_btn {
    background-color: rgb(1, 164, 1); 
    border: 3px solid rgb(1, 164, 1);
    box-shadow: 1px 3px 3px rgb(1, 139, 1);
    transition: all .1s ease;
    margin-bottom: 15px;
}

.login_btn {
    background-color: #1e2749;
    border: 3px solid #0b1126;
    box-shadow: 1px 3px 3px #0b1126;
    transition: all .1s ease;
}

.registration_btn:hover{
    background-color: rgb(2, 187, 2);
}

.login_btn:hover{
    background-color: #171f3a;
}