.lesson_container {
    max-width: 980px;
    padding: 0 10px;
    margin: 0 auto;
}

.firstTest2_container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.firstTest2_content {    
    margin-top: 160px;
    flex-grow: 1;
}

.firstTest2_condition {    
    margin-bottom: 120px;
    font-size: 30px;
}

.firstTest2_answerId {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 10px;
    margin-right: 10px;
    color: #b6bdcb;
    height: 22px;
    width: 22px;
    padding: 5px;
    border: 2px solid #b6bdcb;
    border-radius: 10px;
}

.firstTest2_footer {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    height: 100%;
    margin-bottom: 70px;
    padding-top: 20px;
}

.firstTest2-btn_skip, .firstTest2-btn_check {
    width: 160px;
    height: 40px;
    border-radius: 10px;    
    font-size: 18px;
    cursor: pointer;    
    border-radius: 8px;    
    text-transform: uppercase;   
    font-weight: 600; 
    transition: all .1s ease;
}

.firstTest2-btn_skip {
    color: #b6bdcb;
    background-color: white;  
    border: 1px solid #b6bdcb;
    box-shadow: 1px 3px 3px #b6bdcb;
    opacity: 80%;
}

.firstTest2-btn_check {
    color: rgb(221, 233, 221);
    background-color: rgb(1, 164, 1); 
    border: 1px solid rgb(1, 164, 1);
    box-shadow: 1px 3px 3px rgb(1, 139, 1);    
}

.firstTest2-btn_skip:hover {
    color: #9ba4b8;
    background-color: #e0e2e9;
}

.firstTest2-btn_check:hover {
    background-color: rgb(2, 187, 2);
}

.firstName2_results-form {
    display:flex;
    justify-content:center;
    align-items:center;
    flex-direction: column;
    height: 75vh;
}

.firstTest2_results-p {
    font-size: 30px;    
    margin-bottom: 100px;
}

.firstTest2_robot-img {
    width: 200px;
    height: 200px;
    border: 1px solid black;
    background-image: url("../../assets/images/robotHead2.svg");
}

.firstTest2_results-btn {
    font-size: 24px;
    position: absolute;
    bottom: 0;
    margin-bottom: 120px;
    color: white;
    height: 50px;
    width: 500px;
    background-color: #84b6fc ;
    border: 3px solid #7babee ;
    box-shadow: 1px 3px 3px #7babee;
    border-radius: 8px;
    cursor: pointer;
    
    transition: all .1s ease;
}

.firstTest2_results-btn:hover {
    background-color: #98c1fc ;
}